*{
    padding: 0;
    margin: 0;
}
body{
    background-color: black;
}
.logo{
    margin-right: 10px;
}
.logo>img{
    margin-right: -20px;
    margin-left: 22px;
}
.searchBar {
  background-color: black; /* Set the background color to black */
  padding: 5px;
  border-radius: 5px;
}

/* Style for the search input itself */
.searchBar input[type="text"] {
  border: none;
  background-color: rgb(64, 63, 63); /* Set the background color to black */
  color: white; /* Text color */
  padding: 8px;
  border-radius: 5px;
  width: 500px; /* Adjust width as needed */
}
#btn{
    background-color: black;
    border: 2px solid rgb(60, 58, 58);
    color:rgb(69, 66, 66);
    width:90px;
    height: 30px;
    margin-left: 9px;
    margin-top: 20px;
}
#btn:hover{
    background-color: rgb(114, 112, 112);
}

.image{
    margin-right: 25px;
    margin-top: 30px;
    padding-left: 30px;
}
.image>img{
    margin-left: 20px;
}
.books-container{
    display: flex;
  flex-wrap: wrap;
  justify-content: space-between; /* Adjust alignment as needed */
  gap: -100px;
  margin-top: 50px;

}
.book{
    width: calc(20% - 30px); /* Six books in a row (adjust width and gap as needed) */
  margin-bottom: 5px;
  margin-left: 30px;
}
/* Style for book images */
.book img {
  width: 800px; /* Adjust width as needed */
  height: 150px; /* Adjust height as needed */
  object-fit: cover; /* Ensure uniform image dimensions */
}
.contain{
    display: flex;
    flex-direction: column;
    width:300px;
    margin-top: 200px;
}